import {ColourTheme} from "./theme";

export function darkTheme():ColourTheme {
    return {
        name: "Dark",

        navbar: {
            bg: "#000000"//gray13
        },

        page: {
            bg: "#141414"//gray12
        },

        login: {
            bg: "#1f1f1f",//gray11
            header: {
                color: "#d9d9d9",
                bg: "#262626"//gray10
            },
            input: {
                bg: "#141414", //gray12
                color: "#f5f5f5" //gray3
            }
        }
    }
}
